<template>
  <div class="page-container" v-if="pageNumber > 1">
    <a href="#" @click="handClick(1)" :class="{ disabled: current === 1 }"
      >&lt;&lt;</a
    >
    <a
      href="#"
      @click="handClick(current - 1)"
      :class="{ disabled: current === 1 }"
      >&lt;&lt;</a
    >
    <a
      href="#"
      @click="handClick(n)"
      v-for="(n, i) in numbers"
      :key="i"
      :class="{ active: current === n }"
      >{{ n }}</a
    >
    <a
      href="#"
      @click="handClick(current + 1)"
      :class="{ disabled: current === pageNumber }"
      >&gt;&gt;</a
    >
    <a
      href="#"
      @click="handClick(pageNumber)"
      :class="{ disabled: current === pageNumber }"
      >&gt;&gt;</a
    >
  </div>
</template>

<script>
// 
// 1.
// 2.
// 3.


export default {
  props: {
    // 
    current: {
      type: Number,
      default: 1,
    },
    // 
    total: {
      type: Number,
      default: 100,
    },
    // 
    limit: {
      type: Number,
      default: 10,
    },
    // 
    visibleNumber: {
      type: Number,
      default: 10,
    },
  },
  computed: {
    // 
    pageNumber() {
      return Math.ceil(this.total / this.limit);
    },
    // 
    numbers() {
      let nums = [];
      for (let i = this.visibleMinPage; i <= this.visibleMaxPage; i++) {
        nums.push(i);
      }
      return nums;
    },
    // 
    visibleMinPage() {
      let min = this.current - Math.floor(this.visibleNumber / 2);
      if (min < 1) {
        min = 1;
      }
      return min;
    },
    // 
    visibleMaxPage() {
      let max = this.current + Math.floor(this.visibleNumber / 2) - 1;
      if (max > this.pageNumber) {
        max = this.pageNumber;
      }
      return max;
    },
  },
  methods: {
    // 
    handClick(newpage) {
      if (newpage < 1) {
        newpage = 1;
      }
      if (newpage > this.pageNumber) {
        newpage = this.pageNumber;
      }
      if (newpage == this.current) {
        return;
      }
      // 
      this.$emit("pageChange", newpage);
    },
  },
};
</script>


<style lang="less" scoped>
// ~@ src
@import "~@/styles/var.less";
.page-container {
  display: flex;
  justify-content: center;
  margin: 20px 0;
  a {
    color: @primary;
    margin: 0 6px;
    cursor: pointer;
    &.disabled {
      color: @lightWords;
      cursor: not-allowed;
    }
    &.active {
      color: @words;
      font-weight: bold;
      cursor: text;
    }
  }
}
</style>