<template>
  <div v-if="toc" class="blog-toc-container">
    <h2></h2>
    <RightList :list="toWithSelect" @select="handleSelect" />
  </div>
</template>

<script>
import RightList from "./RightList.vue";
import debounce from "@/utils/debounce.js";
export default {
  data() {
    return {
      activeAnchor: "article-md-title-1",
      // 
      setSelectDebounce: Function,
    };
  },
  components: {
    RightList,
  },
  props: {
    toc: {
      required: true,
    },
  },
  computed: {
    toWithSelect() {
      let getTOC = (toc = []) => {
        return toc.map((item) => {
          return {
            ...item,
            active: this.activeAnchor === item.anchor,
            // active
            children: getTOC(item.children),
          };
        });
      };
      return getTOC(this.toc);
    },
    doms() {
      const doms = [];
      const addDoms = (toc) => {
        for (const i of toc) {
          doms.push(document.getElementById(i.anchor));
          if (i.children && i.children) {
            addDoms(i.children);
          }
        }
      };
      addDoms(this.toc);
      return doms;
    },
  },
  methods: {
    handleSelect(item) {
      location.hash = item.anchor;
    },
    // activeAnchor
    setSelect(scrollDom) {
      if (!scrollDom) {
        return;
      }
      const range = 200;
      for (const dom of this.doms) {
        // dom
        if (!dom) {
          continue;
        }
        const top = dom.getBoundingClientRect().top;
        if (top >= 0 && top <= range) {
          // 
          this.activeAnchor = dom.id;
          return;
        } else if (top > range) {
          // 
          return;
        } else {
          // 
          this.activeAnchor = dom.id;
        }
      }
    },
  },
  created() {
    this.setSelectDebounce = debounce(this.setSelect, 50);
    this.$bus.$on("mainScroll", this.setSelectDebounce);
  },
  destroyed() {
    this.$bus.$off("mainScroll", this.setSelectDebounce);
  },
};
</script>

<style>
.blog-toc-container {
  width: 100%;
}
</style>